#works{
  margin: 15rem auto;
  min-height: calc(100vh - 4rem);
  width: 100vw;
  max-width: 60rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 0 1rem;
}
.workTitle {
  margin: 1rem 0;
  font-size: 3rem;
}
.workDesc {
  font-weight: 300;
  font-size: 1rem;
  max-width: 45rem;
}
.worksImgs{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    width: 100vw;
    max-width: 65rem;
}
.workImg{
    object-fit: cover;
    height: 20rem;
    margin: 0.5rem;
    border-radius: 1rem;
}
.workBtn{
  margin: 3rem 0;
  padding: 0.5rem 2.5rem;
  border: none;
  border-radius: 1rem;
  background:white ;

}
@media screen and (max-width:585px) {

    .workImg{
        height: 48vw;
    }  
}
